.main-timeline {
    overflow: hidden;
    position: relative;
}
.main-timeline:before {
    content: "";
    width: 3px;
    height: 100%;
    background: #adb5bd;
    position: absolute;
    top: 0;
    left: 50%;
}
.main-timeline .timeline {
    padding-right: 30px;
    position: relative;
}
.main-timeline .timeline-icon {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background: #ffffff;
    border: 2px solid #adb5bd;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: -4px;
}
.main-timeline .timeline-icon:before {
    content: "";
    display: block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #30419b;
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}
.main-timeline .year {
    display: inline-block;
    padding: 8px 20px;
    margin: 0;
    font-size: 14px;
    color: #ffffff;
    background: #30419b;
    text-align: center;
    position: absolute;
    top: 50%;
    right: 35%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}
.main-timeline .year:before {
    content: "";
    border-right: 18px solid #30419b;
    border-top: 18px solid transparent;
    border-bottom: 18px solid transparent;
    position: absolute;
    top: 0;
    left: -18px;
}
.main-timeline .timeline-content {
    width: 46.5%;
    padding: 43px 50px;
    margin: 0 20px 0 0;
    background: #f0f4f7;
    position: relative;
}
.main-timeline .timeline-content:after {
    content: "";
    border-left: 20px solid #f0f4f7;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    position: absolute;
    top: 50%;
    right: -20px;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}
.main-timeline .title {
    float: left;
    font-size: 18px;
    margin: 0 20px 20px 0;
}
